@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;0,800;1,500;1,600;1,700;1,800&display=swap");
html {
    background-color: #f2f2f2;
}

body {
    margin: 0;
}
.container {
    width: 70%;
}
.center {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
}
.pageWrapper {
    height: 100vh;
    width: 100vw;
    max-width: 100vw;
}

.verticle-align {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.horizontal-align {
    display: flex;
    justify-content: center;
}

.contentWrapper {
    font-family: "Montserrat", sans-serif;
    border-radius: 40px;
    width: 70%;
    height: 35vh;
    min-height: 525px !important;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: flex;
}

.contentWrapper .left {
    border-radius: 40px 0px 0px 40px;
    background-color: #39394d;
    height: 100%;
    width: 50%;
}

.left .text {
    border-radius: 40px 0px 0px 40px;
    background-color: #39394d;
    padding-left: 15%;
    padding-top: 7%;
}

.left .text ul {
    margin-bottom: 0px;
}

.text h1 {
    font-weight: bold;
    color: white;
    margin: 0;
    margin-bottom: 10px;
}

.text ul {
    padding-left: 15px;
    list-style: none;
    font-size: 18px;
    font-weight: 600;
    color: white;
    font-style: italic;
}

.contentWrapper .right {
    border-radius: 0px 40px 40px 0px;
    background-color: #fdfcfd;
    height: 100%;
    width: 50%;
}

.right {
    overflow: hidden;
}

.login-box h1 {
    text-align: center;
    margin-bottom: 5px;
}
.login-box h5 {
    margin-top: 0px;
    cursor: pointer;
}

.login-box .input-box {
    position: relative;
}

.login-box .input-box input {
    width: 100%;
    padding: 10px 0;
    color: #333;
    margin-bottom: 30px;
    margin-left: 2px;
    border: none;
    border-bottom: 0.5px solid rgba(0, 0, 0, 0.7);
    outline: none;
    background: transparent;
}

.login-box .input-box input:hover {
    border-bottom: 1px solid black;
}

.login-box .input-box input:focus {
    border-bottom: 2px solid #f58220;
}

.noselect {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.login-box .input-box label {
    position: absolute;
    top: 0;
    opacity: 0.57;
    left: 0;
    padding: 10px 0;
    color: #333;
    pointer-events: none;
    transition: 0.3s;
    transform: translateY(20px);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.login-box .input-box input:valid ~ label {
    top: -20px;
    left: 0;
    color: black;
    font-size: 12px;
}

.login-box .input-box input:focus ~ label {
    color: #f58220;
    opacity: 1;
    font-size: 12px !important;
    transition: 0.2s ease-in-out;
}

.login-box .input-box input:empty ~ label {
    font-size: 18px;
}

.login-btn {
    padding: 15px 20px 15px 20px;
    border-radius: 35px;
    width: 50%;
    border: none;
    color: white;
    background-color: #f58220;
    font-weight: 650;
    font-size: 20px;
    margin-top: 5%;
}

@media (max-width: 1100px) {
    .container {
        width: 95%;
    }
    .text ul {
        margin-bottom: 15px;
    }
    .login-btn {
        margin-bottom: 15px;
    }
    .verticle-align {
        height: 100%;
        width: 100%;
        display: inline-block !important;
    }

    .horizontal-align {
        height: 100%;
        width: 100%;
        display: inline-block !important;
    }

    .contentWrapper {
        width: 100%;
        height: auto;
        border-radius: 0px;
        display: flex;
        flex-direction: column-reverse;
        justify-content: start !important;
        align-content: flex-start !important;
        box-shadow: none;
    }

    html {
        background-color: #39394d;
    }
    .contentWrapper .left {
        border-radius: 0px;
        width: 100%;
    }

    .contentWrapper .right {
        border-radius: 0px;
        width: 100%;
        min-height: 50vh !important;
    }
}
